type CadenzaAutocompleteAllowedTypes =
  | 'tag'
  | 'release'
  | 'composer'
  | 'track'

interface CadenzaAutocompleteResultsTypeTagInterface {
  category?: string
  parent?: string
  name?: string
  id?: string // Select key ?tags=value
}

export interface CadenzaAutocompleteResultsTypeComposerInterface {
  firstName?: string
  lastName?: string
  middleName?: string
  slug?: string // Select key ?composers=value
}

interface CadenzaAutocompleteResultsTypeReleaseInterface {
  title?: string
  totalTracks?: number
  count?: number
  slug?: string // Select key ?releases=value
}

interface CadenzaAutocompleteResultsTypeTrackInterface {
  title?: string
  slug?: string // Select key ?tracks=value
}

export enum CadenzaAutocompleteMapTypeByQuery {
  tags = 'tag',
  releases = 'release',
  composers = 'composer',
  tracks = 'track',
  q = 'q'
}

export interface CadenzaAutocompleteInterface extends CadenzaAutocompleteResultsTypeTagInterface, CadenzaAutocompleteResultsTypeComposerInterface, CadenzaAutocompleteResultsTypeReleaseInterface, CadenzaAutocompleteResultsTypeTrackInterface {
  _id: string
  type: CadenzaAutocompleteAllowedTypes
  searchIndex: string
  order?: number // Used for custom order when grouping by type
}

export interface CadenzaAutocompleteResultsGroupedByTypeInterface {
  [key: string]: CadenzaAutocompleteInterface[]
}
