
































import { Vue, Component, Prop } from 'vue-property-decorator'
import slugify from 'slugify'
import escapeRegExp from 'lodash/escapeRegExp'
import {
  CadenzaAutocompleteInterface, CadenzaAutocompleteMapTypeByQuery, CadenzaAutocompleteResultsTypeComposerInterface
} from '@/interfaces/CadenzaAutocompleteInterface'

@Component
export default class CadenzaAutocompleteTag extends Vue {
  @Prop({ default: null })
  tag!: CadenzaAutocompleteInterface

  @Prop({ default: '' })
  acValue!: string

  @Prop({ default: true })
  showTypeIcon!: boolean

  @Prop({ default: '', required: true })
  acRedirectUrl!: string

  @Prop({ default: true })
  openInNewTab!: boolean

  get returnHref (): string {
    let queryKey = ''
    let queryValue = ''
    if (this.tag) {
      switch (this.tag.type) {
        case 'release':
          queryKey = 'releases'
          queryValue = this.tag.slug || ''
          break
        case 'track':
          queryKey = 'tracks'
          queryValue = this.tag.slug || ''
          break
        case 'tag':
          queryKey = 'tags'
          queryValue = this.tag.id || ''
          break
        case 'composer':
          queryKey = 'composers'
          queryValue = this.tag.slug || ''
          break
      }
    }
    return `${this.acRedirectUrl}/tracks?${queryKey}=${queryValue}`
  }

  get returnTagCategoryClass (): string {
    let slug = ''
    if (this.tag) {
      if (this.tag.category) {
        slug = this.tag.category
      } else if (this.tag.type) {
        slug = this.tag.type
      }
    }
    return slugify(slug, {
      lower: true,
      remove: /[*+~.()/'"?!;:@]/g
    })
  }

  get returnTagName (): string {
    const dataObject = this.tag
    if (!dataObject) return ''
    let formatTagName = ''
    switch (dataObject.type) {
      case CadenzaAutocompleteMapTypeByQuery.tags:
        if (dataObject.category) {
          formatTagName += `<span class="tag--category">${this.highlightTag(
            dataObject.category
          )}</span>`
        }
        if (dataObject.parent) {
          formatTagName += `<span class="tag--parent">${this.highlightTag(
            dataObject.parent
          )}</span>`
        }
        if (dataObject.name) {
          formatTagName += `<span class="tag--name">${this.highlightTag(
            dataObject.name
          )}</span>`
        }
        return formatTagName || dataObject._id
      case CadenzaAutocompleteMapTypeByQuery.releases:
        return (
          this.highlightTag(dataObject.title || '') ||
          `[missing-title] ${CadenzaAutocompleteMapTypeByQuery.releases}`
        )
      case CadenzaAutocompleteMapTypeByQuery.composers:
        return this.highlightTag(
          this.formatComposerName(
            dataObject as CadenzaAutocompleteResultsTypeComposerInterface
          ) || ''
        )
      case CadenzaAutocompleteMapTypeByQuery.tracks:
        return (
          this.highlightTag(dataObject.title || '') ||
          `[missing-title] ${CadenzaAutocompleteMapTypeByQuery.tracks}`
        )
      default:
        return `Not defined for type - ${dataObject.type}`
    }
  }

  get returnTagIcon (): string {
    const dataObject = this.tag
    if (!dataObject) return ''
    switch (dataObject.type) {
      case CadenzaAutocompleteMapTypeByQuery.tags:
        return 'fas fa-tag'
      case CadenzaAutocompleteMapTypeByQuery.releases:
        return 'fas fa-compact-disc'
      case CadenzaAutocompleteMapTypeByQuery.composers:
        return 'fas fa-user'
      case CadenzaAutocompleteMapTypeByQuery.tracks:
        return 'fas fa-music'
      default:
        return ''
    }
  }

  highlightTag (tag: string): string {
    return tag.replace(new RegExp(escapeRegExp(this.acValue), 'gi'), (match) => {
      return '<span part="highlighted__text" class="highlighted__text">' + match + '</span>'
    })
  }

  formatComposerName ({
    firstName,
    lastName,
    middleName
  }: CadenzaAutocompleteResultsTypeComposerInterface): string {
    let fullName = ''
    if (firstName) {
      fullName += firstName
    }
    if (middleName) {
      fullName += ` ${middleName}`
    }
    if (lastName) {
      fullName += ` ${lastName}`
    }
    return fullName
  }
}
