













































































































import { Vue, Component, Prop } from 'vue-property-decorator'
import {
  CadenzaAutocompleteInterface,
  CadenzaAutocompleteResultsGroupedByTypeInterface
} from '@/interfaces/CadenzaAutocompleteInterface'
import mapValues from 'lodash/mapValues'
import groupBy from 'lodash/groupBy'
import CadenzaAutocompleteTag from '@/components/CadenzaAutocompleteTag/CadenzaAutocompleteTag.vue'

interface OrderInterface {
  tag: number,
  release: number,
  track: number,
  composer: number
}

@Component({
  components: { CadenzaAutocompleteTag }
})
export default class CadenzaAutocomplete extends Vue {
  @Prop({ default: 1 })
  orderTag!: string | number

  @Prop({ default: 2 })
  orderRelease!: string | number

  @Prop({ default: 3 })
  orderTrack!: string | number

  @Prop({ default: 4 })
  orderComposer!: string | number

  @Prop({ default: 'https://api.qa.cadenzabox.com', required: true })
  baseApiUrl!: string

  @Prop({ default: 'https://audiomachine.qa.cadenzabox.com', required: true })
  acRedirectUrl!: string

  @Prop({ default: false })
  groupByType!: string

  @Prop({ default: false })
  showTypeIcon!: boolean

  @Prop({ default: false })
  uiSearchOutside!: boolean

  @Prop({ default: false })
  uiSubmitOutside!: boolean

  @Prop({ default: false })
  openInNewTab!: boolean

  @Prop({ default: 'Sound Design' })
  categoryName!: string

  @Prop({ default: 'Popular tags' })
  categoryLabel!: string

  @Prop({ default: 'Search here...' })
  inputSearchPlaceholder!: string

  acValue = ''
  acResults: CadenzaAutocompleteInterface[] = []
  tagsByCategoryResults = []

  mounted (): void {
    if (this.categoryName) {
      this.onShowTagsByCategory(this.categoryName)
    }
  }

  get returnBaseApiUrl (): string {
    return this.baseApiUrl
  }

  async onAutocomplete (): Promise<void> {
    let url = `${this.returnBaseApiUrl}/autocomplete`
    if (this.acValue) {
      url += `?ac=${encodeURIComponent(this.acValue)}`
    }
    // GET request using fetch with error handling
    fetch(url)
      .then(async response => {
        const data = await response.json()

        // check for error response
        if (!response.ok) {
          // get error message from body or default to response statusText
          const error = (data && data.message) || response.statusText
          return Promise.reject(error)
        }

        this.acResults = data || []
      })
      .catch(error => {
        console.error('There was an error!', error)
      })
  }

  async onShowTagsByCategory (category: string): Promise<void> {
    const url = `${this.returnBaseApiUrl}/tag-manager/${encodeURIComponent(category)}?$limit=999`
    // GET request using fetch with error handling
    fetch(url)
      .then(async response => {
        const data = await response.json()

        // check for error response
        if (!response.ok) {
          // get error message from body or default to response statusText
          const error = (data && data.message) || response.statusText
          return Promise.reject(error)
        }

        if (data && data.children && data.children.data) {
          this.tagsByCategoryResults = data.children.data || []
        }
      })
      .catch(error => {
        console.error('There was an error!', error)
      })
  }

  get returnResults (): CadenzaAutocompleteInterface[] {
    return this.acResults
      .map((obj) => {
        switch (obj.type) {
          case 'tag':
            obj.order = +this.orderTag
            break
          case 'release':
            obj.order = +this.orderRelease
            break
          case 'track':
            obj.order = +this.orderTrack
            break
          case 'composer':
            obj.order = +this.orderComposer
            break
        }
        return obj
      })
      .sort((a, b) => {
        let aOrder = 1
        let bOrder = 1
        if (a && a.order) {
          aOrder = a.order
        }
        if (b && b.order) {
          bOrder = b.order
        }
        return aOrder <= bOrder ? -1 : 1
      })
  }

  get returnGroupedByType (): CadenzaAutocompleteResultsGroupedByTypeInterface {
    return mapValues(
      groupBy(this.returnResults, 'type')
    ) as CadenzaAutocompleteResultsGroupedByTypeInterface
  }

  onQuerySearch (): void {
    const url = `${this.acRedirectUrl}/tracks?q=${this.acValue}`
    if (this.openInNewTab) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      window.open(url, '_blank').focus()
    } else {
      window.location.href = url
    }
  }
}
